<template>
    <div>
        <crumbs menu='实时数据' subMenu='客流量' /> 
        <div class=" container sql">
          <el-row type="flex" justify="space-around">
          <el-col :span='7'  >
            <market @marketGet='marketGet'/>
          </el-col>
          <el-col :span='7'>
            选择日期:
            <el-date-picker
                v-model="timeValue"
                value-format="yyyy-MM-dd"
                align="right"
                :clearable='false'
                type="date"
                placeholder="选择日期"
                :disabled-date="disabledDate"
                :shortcuts="shortcuts"
              >
            </el-date-picker>
          </el-col>
          
            <el-col :span='7'>
            <el-button type="primary" icon="el-icon-search" @click='onSubmit'>查询</el-button>
            <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>
            <export-excel :file-name="name" :header="exportItem.map(item=>item.label)"
              :filter-filed="exportItem.map(item=>item.value)" :data="exportTable">导出</export-excel>
          </el-col>
        </el-row>
          
        
        </div>
        <div class="allCard">
          <el-row :gutter="20" class="mgb20">
                    <el-col :span="12">
                        <el-card shadow="hover" :body-style="{ padding: '0px' }">
                            <div class="grid-content grid-con-1">
                                <i class="el-icon-user-solid grid-con-icon"></i>
                                <div class="grid-cont-right">
                                    <div class="grid-num">{{chartList[0]}}</div>
                                    <div>今日客流量</div>
                                </div>
                            </div>
                        </el-card>
                    </el-col>
                    <el-col :span="12">
                        <el-card shadow="hover" :body-style="{ padding: '0px' }">
                            <div class="grid-content grid-con-2">
                                <i class="el-icon-message-solid grid-con-icon"></i>
                                <div class="grid-cont-right">
                                    <div class="grid-num">{{chartList[1]==0?'未选择对比时间':chartList[1]}}</div>
                                    <div>当日客流量</div>
                                </div>
                            </div>
                        </el-card>
                    </el-col>
                </el-row>
        </div>
        <el-card header="客流量对比图" style="font-weight: bold;">
            <div ref="myEchart1" style="height: 350px;width:100%;" >
            </div>
        </el-card>
        <div>
          <el-dialog title="数据导出" v-model="dialogFormVisible" >
            <el-form :model='form'>
              <el-form-item label="商场名称:" :label-width="formLabelWidth">
                  {{marketName}}
              </el-form-item>
              <el-form-item label="数据维度:" :label-width="formLabelWidth">
                  <el-radio-group v-model="form.resource" size="medium">
                    <el-radio border label="当日数据导出"></el-radio>
                    <el-radio border label="查询数据导出"></el-radio>
                    <el-radio border label="对比数据导出"></el-radio>
                  </el-radio-group>
              </el-form-item>
              <el-form-item label="导出时间" :label-width="formLabelWidth">
                  <el-time-select
                    placeholder="起始时间"
                    v-model="form.startTime"
                    :start='startTime'
                    step='01:00'
                    :end="endTime"
                    >
                  </el-time-select>
                  <el-time-select
                    placeholder="结束时间"
                    v-model="form.endTime"
                    :start='startTime'
                    step='01:00'
                    :end="endTime"
                    :minTime='form.startTime'
                    >
                  </el-time-select>
              </el-form-item>
            </el-form>
            <template v-slot:footer class="dialog-footer">
              <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
              <el-button @click="dialogFormVisible = false">取 消</el-button>
            </template>
          </el-dialog>
        </div>
    </div>
    
</template>

<script>
import Crumbs from '../pages/Crumbs'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import axios from 'axios'
import ExportExcel from '../pages/excel/ExportExcel'
import * as echarts from 'echarts'

export default {
    name: 'passengerFlow',
    data() {
        return {
        exportTable:[],
        exportItem:[
        ],
        disabledDate:(time)=>{
            return time.getTime()>this.$moment()
        },
          //商场
        marketName:'万达',
        startTime:'',
        endTime:'',
        chartValue:[],
        mallStartTime:'',//商场开始营业时间
        mallEndTime:'',//商场关门时间
          //表格
        chart:{},//存储初始化后的chart
        timeValue:'',//选择的时间
        chartData:[],//chart的series
        chartList:[0,0],//chart的数据保存
        defaultChartList:[0,0],
          //弹框
        dialogTableVisible: false,//弹框
        dialogFormVisible: false,//弹框
        form:{//表单相关
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: '',
          startTime: '9:00',
          endTime: '21:00',
        },
        formLabelWidth: '120px',//表单宽度
        screenWidth: document.body.clientWidth,   
        shortcuts: [{
                text: '昨天',
                value: (() => {
                    return this.$moment().subtract(1, 'days')
                })()
            }]
        };

    },
    methods: {
        onSubmit() {
            this.$message.success('查询成功');
            this.chart.clear()
            console.log(this.timeValue)
            axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/querySaleFlow?time=${13}`).then(res=>{
            let getData=res.data.data.flow
            this.chartList[1]=getData.reduce((prev, next) => prev + next)
            this.chartData[1]={
                name:this.$moment(this.timeValue).format('YYYY-MM-DD') ,
                type: 'line',
                stack: '总量',
                areaStyle: {
                opacity: 0.8,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(55, 162, 255)'
                }, {
                    offset: 1,
                    color: 'rgba(116, 21, 219)'
                }])
                },
                emphasis: {
                    focus: 'series'
                },
                data: getData
            }

            this.chart=this.initChart(this.$refs.myEchart1,this.chartData)
            let timeRangeData=['9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00']
            let newObj={}
            for(let i=0;i<timeRangeData.length;i++){
                newObj[timeRangeData[i]]=getData[i]
            }
            newObj['dateTime']=this.$moment(this.timeValue).format('YYYY-MM-DD') 
            newObj['marketName']=this.marketName
            this.exportTable[1]=(newObj)
            })
        },
        initChart(chart,data){
            let myChart =echarts.init(chart);
            let option = {
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'cross',
                    label: {
                        backgroundColor: '#6a7985'
                    }
                }
            },
            legend: {
                data: [this.$moment().format('YYYY-MM-DD'),this.$moment(this.timeValue).format('YYYY-MM-DD')]
            },
            toolbox: {
                feature: {
                    saveAsImage: {},
                    dataZoom: {
                        yAxisIndex: "none"
                    },
                    // dataView: {
                    //     readOnly: false
                    // },
                    magicType: {
                        type: ["line", "bar"]
                    },
                    // restore: {}
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    boundaryGap: false,
                    data: ['9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00']
                }
            ],
            yAxis: [
                {
                    type: 'value'
                }
            ],
            series: data
            };
            
            myChart.setOption(option);
            return myChart
        },
        reSet(){
        this.chart.clear()
            this.initChart(this.$refs.myEchart1,this.chartData)
            this.chartList=this.defaultChartList
            this.timeValue=this.$moment().subtract(1,'days')
        this.$message.success('重置成功');
        },
        marketGet(val){
            this.marketName=val
        },
    },
    mounted(){
        let timeRangeData=['9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00']
        this.exportItem.push({
            value:"dateTime",
            label:'日期'
        })
        this.exportItem.push({
            value:"marketName",
            label:'商场名称'
        })
        for(let i=0;i<timeRangeData.length;i++){
            this.exportItem.push({
                value:timeRangeData[i],
                label:timeRangeData[i]
            })
        }
        this.timeValue=this.$moment().subtract(1, 'days')
        //echarts宽度刷新
        const that = this
        window.onresize = () => {
        return (() => {
            window.screenWidth = document.body.clientWidth
            that.screenWidth = window.screenWidth
        })()
    }
    //今日数据获取
        let today=this.$moment().format('YYYY-MM-DD')
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/querySaleFlow?time=${13}`).then(res=>{
            let currentData=res.data.data.flow
            this.startTime=res.data.data.startTime
            this.endTime=res.data.data.endTime
            this.chartList[0]=currentData.reduce((prev, next) => prev + next)
            this.defaultChartList[0]=this.chartList[0]
            this.chartData.push({
                name:`${today}`,
                type: 'line',
                stack: '总量',
                areaStyle: {
                    opacity: 0.8,
                    color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(128, 255, 165)'
                }, {
                    offset: 1,
                    color: 'rgba(1, 191, 236)'
                }])
                },
                emphasis: {
                    focus: 'series'
                },
                data: currentData
            })
            console.log(res.data.data)
            this.chart=this.initChart(this.$refs.myEchart1,this.chartData)
            let newObj={}
            for(let i=0;i<timeRangeData.length;i++){
                newObj[timeRangeData[i]]=currentData[i]
            }
            newObj['dateTime']=today
            newObj['marketName']=this.marketName
            this.exportTable.push(newObj)
    })
    axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/querySaleFlow?time=${13}`).then(res=>{
            let getData=res.data.data.flow
            this.chartList[1]=getData.reduce((prev, next) => prev + next)
            this.defaultChartList[1]=this.chartList[1]
            this.chartData[1]={
                name:this.$moment(this.timeValue).format('YYYY-MM-DD') ,
                type: 'line',
                stack: '总量',
                areaStyle: {
                opacity: 0.8,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(55, 162, 255)'
                }, {
                    offset: 1,
                    color: 'rgba(116, 21, 219)'
                }])
                },
                emphasis: {
                    focus: 'series'
                },
                data: getData
            }

            this.chart=this.initChart(this.$refs.myEchart1,this.chartData)
             let newObj={}
            for(let i=0;i<timeRangeData.length;i++){
                newObj[timeRangeData[i]]=getData[i]
            }
            newObj['dateTime']=this.$moment(this.timeValue).format('YYYY-MM-DD') 
            newObj['marketName']=this.marketName
            this.exportTable[1]=(newObj)
            })
    

    },
    components:{
      crumbs:Crumbs,
      market:MarketSelect,
    // chart:Chart
    ExportExcel

    },
    computed:{
        name(){
            return `${this.marketName}商场客流数据对比-${this.$moment().format('YYYYMMDD')}`
        }
    },
    watch:{
        screenWidth(val){
        // 为了避免频繁触发resize函数导致页面卡顿，使用定时器
        if(!this.timer){
            // 一旦监听到的screenWidth值改变，就将其重新赋给data里的screenWidth
            this.screenWidth = val
            this.timer = true
            let that = this
            setTimeout(function(){
                // 打印screenWidth变化的值
                console.log(that.screenWidth)
                that.timer = false
                that.chart.resize()
            },400)
        }
    }
}
};
</script>
<style lang="css" scoped>
.sql{
    padding: 15px;
  }

/* 浮动 */
.grid-content {
    display: flex;
    align-items: center;
    height: 150px;
}

.grid-cont-right {
    flex: 1;
    text-align: center;
    font-size: 20px;
    color: #999;
}

.grid-num {
    font-size: 40px;
    font-weight: bold;
}

.grid-con-icon {
    font-size: 70px;
    width: 150px;
    height: 150px;
    text-align: center;
    line-height: 150px;
    color: #fff;
}

.grid-con-1 .grid-con-icon {
    background: rgb(45, 140, 240);
}

.grid-con-1 .grid-num {
    color: rgb(45, 140, 240);
}

.grid-con-2 .grid-con-icon {
    background: rgb(100, 213, 114);
}

.grid-con-2 .grid-num {
    color: rgb(45, 140, 240);
}

.grid-con-3 .grid-con-icon {
    background: rgb(242, 94, 67);
}

.grid-con-3 .grid-num {
    color: rgb(242, 94, 67);
}
.mgb20 {
    margin-bottom: 20px;
    margin-top: 10px;
}
</style>