// 实时指标排行
<template>
  <div>
    <div>
      <crumbs menu='实时数据' subMenu='实时指标排行' /> 
      <div class=" container sql">
          <el-row type="flex" justify="space-around">
          <el-col :span=6>
            <market @marketGet='marketGet' :marketSet='marketName'/>
          </el-col>
          <el-col :span='6'  >
            指标名称:
            <el-select v-model="indexName" placeholder="请选择" filterable>
              <el-option
                v-for="type in indexType" :label="type.key" :value="type.value" :key="type.value">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span='6'>
            区域分类:
           <el-select v-model="areaName" placeholder="请选择" filterable>
              <el-option
                v-for="type in regionType" :label="type.key" :value="type.value" :key="type.value">
              </el-option>
            </el-select>
          </el-col>
            <el-col :span='6'>
            <el-button type="primary" icon="el-icon-search" @click='onQuery'>查询</el-button>
            <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>
            <!-- <el-button type="primary" icon="el-icon-document">导出</el-button> -->
          </el-col>
        </el-row>    
      </div>
      <el-card class="marginTop10" shadow="hover">
                  <el-row>
                      <el-table class="font-set"
                        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                        style="width: 100%;margin-bottom: 10px;margin-top:10px;font-size: 9px"
                        border
                        @sort-change="sort_change"
                        :current-page="currentPage"              
                        stripe
                      >
                      <el-table-column
                        prop="rank"
                        label="排名"
                        sortable
                        align="center"
                        min-width="20%">
                      </el-table-column>
                      <el-table-column
                        prop="area"
                        label="区域"
                        align="center"
                      
                        min-width="20%">
                      </el-table-column>
                      <el-table-column
                        prop='data'
                        :label="indexName"
                        align="center"
                        sortable
                        min-width="20%">
                      </el-table-column>
                      <el-table-column
                        prop="market"
                        label="商场"
                       
                        align="center"
                        min-width="20%">
                      </el-table-column>
                    </el-table>
            </el-row>
              <div class="pagination flexCenter" >
                  <el-pagination 
                      class="flexCenter"
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :page-sizes="[10,30,50]"
                      :page-size="pagesize"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="tableData.length">     
                  </el-pagination>
              </div>
    </el-card>
    </div>
  </div>
</template>
<script>
import Crumbs from '../pages/Crumbs'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import axios from 'axios'
export default {
  data(){
    return {
      indexName:'客流量',
      marketName:'万达',
      areaName:'全部',
      currentPage: 1,
			pagesize:10,
      tableData:[],
      temList:[],
      indexType: [
        { key: '客流量', value: '客流量' },
        { key: '销售额', value: '销售额' }
      ],
      regionType: [
        { key: '安全通道', value: '安全通道' },
        { key: '1号楼出口', value: '1号楼出口' },
        { key: '2号楼入口', value: '2号楼入口' },
        { key: '1号楼1层', value: '1号楼1层' },
        { key: '1号楼2层', value: '1号楼2层' },
        { key: '2号楼1层', value: '2号楼1层' },
        { key: '2号楼2层', value: '2号楼2层' },
        { key: '全部', value: '全部' }
      ],
      formInline: {
        page: 1,
        limit: 10,
        machineNo: '',
        orderNo: '',
        transId: '',
        indexType: 0,
        orderStatus: 0,
        marketName:'',
        token: localStorage.getItem('logintoken')
      },
      
    }
  },
  components:{
    market:MarketSelect,
    crumbs:Crumbs,

  },
  mounted(){
    axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/realtimeIndexRanking').then(res=>{
      console.log(res.data.list)
      let sortList= res.data.list.sort((a,b)=>{ return b.flow-a.flow})
      for(let i=0;i<sortList.length;i++){
        sortList[i].rank=i+1
      }
      let newArry=[]
      for(let i=0;i<sortList.length;i++){
        newArry.push({
          rank:sortList[i].rank,
          data:sortList[i].flow,
          market:this.marketName,
          area:sortList[i].area
        })
      }
      console.log(newArry)
      this.tableData=[...newArry]
    })
  },
  methods:{
    marketGet(val){
      this.marketName=val
    },
     sort_change(params){  
        console.log(params)
        if(params.order=="ascending"){
          this.tableData.sort((a,b)=>a[params.prop]-b[params.prop])
        }else if(params.order=="descending"){
          this.tableData.sort((a,b)=>b[params.prop]-a[params.prop])
        }else {
          this.tableData.sort((a,b)=>a.rank-b.rank)
        }
        
      },
    handleSizeChange(val) {
          this.pagesize=val;
      },
      handleCurrentChange(val) {
          this.currentPage = val;
      },
    onQuery(){
      axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/realtimeIndexRanking').then(res=>{
      let sortList=res.data.list
      if(this.areaName!='全部'){
        sortList=sortList.filter(item=>item.area==this.areaName)
      }
      if(this.indexName=='客流量'){
        sortList=sortList.sort((a,b)=>{ return b.flow-a.flow})
      }else{
        sortList=sortList.sort((a,b)=>{ return b.sale-a.sale})
      }
      for(let i=0;i<sortList.length;i++){
        sortList[i].rank=i+1
      }
      let newArry=[]
      for(let i=0;i<sortList.length;i++){
        newArry.push({
          rank:sortList[i].rank,
          data:this.indexName=='客流量'?sortList[i].flow:sortList[i].sale,
          market:this.marketName,
          area:sortList[i].area
        })
      }
      this.tableData=[...newArry]  
    })
    },
    reSet(){
      this.indexName='客流量'
      this.areaName='全部',
      this.marketName='万达'
      this.onQuery()
    }
  },
  computed:{
    _fileName(){
      return `${this.marketName}商场-${this.indexName}-${this.areaName}-${this.$moment().format('YYYYMMDD')}`
    }
  }
  
}
</script>
<style lang="css" scoped>
.borderSet{
    /* border-right: 2px solid black; */
    border-left: 2px solid #C0C4CC;
  }
  .flexCenter{
    display: flex;
    align-items: center; /*垂直方向居中*/
    justify-content: flex-end;  /*水平方向居中*/
    /* flex-wrap: wrap; */
  }
  .flexJus{
    display: flex;
    justify-content: space-around;  /*水平方向居中*/
  }
 .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
  .card-header span{
    font-size: 20px;
    font-weight: bolder;
  }
  .fontSet{
     font-size: 18px;
    font-weight: bolder;
  }
  .fontCenter{
    display: flex;
    align-items: center; /* 垂直方向居中 */
    justify-content: space-around;

  }
</style>